@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,800);
/* font-family: 'Open Sans', sans-serif; */


body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  background-color: #F2F2F2;
}

.App__parent {
  width: 100%;
  min-width: 1000px;
  min-height: 100vh;
}

.App__content {
  width: 1000px;
  margin: 0 auto;
  min-height: calc( 100vh - 50px );
  box-sizing: border-box;
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.Header__parent {
  width: 100%;
  height: 50px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.12), 0 0 2px 0 rgba(0, 0, 0, 0.14);
  background-color: #FFF;
}

.Header__content {
  width: 1000px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0px 20px;
}

.Header__company-info {
  font-size: 18px;
  display: flex;
  align-items: center;
  margin-right: 10px;
}

#Header__company-icon {
  font-size: 35px;
  color: #2aabe2;
  margin-right: 5px;
}

.Header__profile {
  border: 1px solid #2aabe2;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
}

.Header__right {
  display: flex;
  height: 100%;
  align-items: center;
}
.Search__parent {
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0px 10px;
}

.Search__parent input {
  border-radius: 21px;
  border: 1px solid #2aabe2;
  outline: none;
  padding: 9px 34px 8px 12px;
  font-size: 12px;
}

.Search__content {
  position: relative;
}

#Search__icon {
  font-size: 20px;
  position: absolute;
  top: 6px;
  right: 12px;
  cursor: pointer;
}
.Compose__parent {
  width: 100%;
  background-color: #FFF;
  border-radius: 5px;
  margin-bottom: 30px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.12), 0 0 2px 0 rgba(0, 0, 0, 0.14);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  box-sizing: border-box;
  padding: 20px;
}

.Compose__top {
  display: flex;
  justify-content: center;
  width: 100%;
}

.Compose__profile-picture {
  border: 1px solid #2aabe2;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.Compose__input {
  border-radius: 5px;
  border: 1px solid #2aabe2;
  padding: 8px;
  width: 100%;
  outline: none;
}

.Compose__bottom {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.Compose__bottom button {
  color: #FFF;
  background-color: #2aabe2;
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
}
.Post__parent {
  width: 100%;
  min-height: 150px;
  background-color: #FFF;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.12), 0 0 2px 0 rgba(0, 0, 0, 0.14);
  border-radius: 5px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 10px 20px;
  box-sizing: border-box;
  position: relative;
}

.Post__master-controls {
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 20px;
  cursor: pointer;
}

.Post__master-menu {
  display: none;
  position: absolute;
  width: 100px;
  right: -105px;
  background-color: #FFF;
  flex-direction: column;
  padding: 10px 10px 0px 10px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.12), 0 0 2px 0 rgba(0, 0, 0, 0.14);
}

.Post__master-menu span {
  margin-bottom: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Post__master-menu span:hover {
  color: #2aabe2;
}

.Post__meta-data {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
}

.Post__profile-picture {
  border: 1px solid #2aabe2;
  display: flex;
  border-radius: 50%;
  height: 25px;
  width: 25px;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
}

.Post__name {
  font-weight: 600;
  font-size: 20px;
  margin-right: 5px;
}

.Post__handle {
  font-size: 13px;
  margin-right: 5px;
}

.Post__date {
  font-size: 13px;
}

.Post__content {
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  padding: 10px;
}

.Post__user-controls {
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  padding: 10px;
  font-size: 20px;
}

.Post__control-icon {
  margin-right: 10px;
  cursor: pointer;
}
.Edit__parent {
  width: 100%;
  height: 90px;
}

.Edit__textarea {
  width: 99%;
  height: 50px;
  resize: none;
}

.Edit__controls {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.Edit__control-btn {
  color: #FFF;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
}

#Edit__controls-update {
  margin-right: 10px;
  background-color: #2aabe2;
}

#Edit__controsl-cancel {
  background-color: grey;
}
